import { Component, h, Element, Prop, Method } from '@stencil/core';

@Component({
	tag: 'ks-order-form-modal',
    styleUrl: "orderFormModal.css"
})
export class OrderLoginPrompt {
    @Element() root: HTMLElement;
    @Prop({mutable: true}) message: string = "";
    @Prop() back: string = "Wróć";

    @Method()
    async Show(message?: string) {
        this.message = message;
        this.root.querySelector("ks-dialog").show();
    }

    @Method()
    async Hide() {
        this.root.querySelector("ks-dialog").hide();
    }

	render() {
        return <ks-dialog dark>
            <h1>Komunikat</h1>
            <p>{this.message}</p>
            <ks-button round name={this.back} onClick={()=>this.Hide()} />
        </ks-dialog>;
    }

}
